.toggle {
  display: block;
  width: 21px;
  height: 21px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.toggle__bar {
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.toggle__bar::before,
.toggle__bar::after {
  content: "";
  display: block;
  position: absolute;
}
.toggle__bar,
.toggle__bar::before,
.toggle__bar::after {
  width: 100%;
  height: 2px;
  left: 0;
  background-color: #231f20;
  transition: transform 0.3s linear;
}
.toggle__bar::before {
  top: 6px;
}
.toggle__bar::after {
  bottom: 6px;
}
.toggle--activ .toggle__bar::after {
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
  transition: transform 0.3s linear;
}
.toggle--activ .toggle__bar::before {
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  transition: transform 0.3s linear;
}
.toggle--activ .toggle__bar {
  width: 27px;
  background-color: transparent;
  transition: transform 0.3s linear;
}
@media (min-width: 768px) {
  .toggle {
    display: none;
  }
}
