.card__image {
  display: block;
  min-width: 260px;
  width: 100%;
  min-height: 195px;
  height: auto;
}

.card__info {
  padding: 27px 28px 32px;
  border: 1px solid @color-light-grey;
}

.card__description {
  font-size: 13px;
}

.card__buy {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin-top: 17px;
  padding-top: 22px;
}

.card__buy::before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: @color-light-grey;
  top: 0;
}

.card__price {
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: @color-black;
}

.card__button {
  width: 20px;
  height: 20px;
  margin: 0;
  padding: 0;
  border: none;
}
